.page {
  position: relative;
  z-index: auto;
  overflow: visible;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  line-height: 22.4px;
  color: $main-text-color;

  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.3s ease-in-out;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
